<template>
  <div class="me-detail">
    <van-nav-bar
    fixed
    title="个人信息"
    left-arrow
    @click-left="onClickLeft">
    </van-nav-bar>
    <div class="me-detail-img me-detail-common border">
      <span>头像</span>
      <img :src="user.avatar" alt="">
    </div>
    <div class="me-detail-common border">
      <span>姓名</span>
      <span>{{user.name}}</span>
    </div>
    <div class="me-detail-common border">
      <span>手机</span>
      <span>{{user.phone}}</span>
    </div>
  </div>
</template>

<script>
import { NavBar } from 'vant'
import defaultAvatar from '@/assets/img/default-avatar.png'

export default {
  components: {
    [NavBar.name]: NavBar
  },
  data () {
    return {
      user: {
        avatar: defaultAvatar,
        name: '',
        phone: ''
      }
    }
  },
  methods: {
    onClickLeft () {
      this.$router.push({
        path: '/me/index',
        query: {}
      })
    }
  }
}
</script>

<style scoped>
.me-detail{
  font-size: 14px;
  padding-top: 46px;
}
.me-detail-common{
  display:flex;
  justify-content: space-between;
  height: 50px;
  align-items: center;
  padding: 0 10px;
}
.me-detail-common>span:first-child{
  color: #000;
}
.me-detail-common>span:nth-child(2){
  color: #999;
  font-size: 14px;
}
.me-detail-img{
  height: 80px;
}
.me-detail-img>img{
  width: 60px;
  border-radius: 3px;
}
</style>



